.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0 1px 3px -2px black;
    background-color: white;
    height: 70px;
    width: 100%;
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
}

.nav-logo {
    display: flex;
    align-items: center;
    gap: 10px;
    padding-left: 20px;
}

.nav-logo img {
    width: auto;
    height: 50px;
}

.nav-menu {
    display: flex;
    align-items: center;
    list-style: none;
    gap: 30px;
    color: black;
    font-size: 20px;
    font-weight: 500;
}

.nav-menu li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}

.nav-menu hr {
    width: 50px;
    height: 4px;
    border-radius: 10px;
    background-color: black;
}

.nav-search-login-cart {
    display: flex;
    gap: 20px;
    padding-right: 20px;
}

.nav-search {
    display: flex;
    align-items: center;
    flex: 1;
    border: 2px solid black;
    border-radius: 50px;
    padding: 2px 16px;
    margin: 0;
}

.nav-search input {
    border: none;
    outline: none;
    font-size: 16px;
    background: none;
}

#search_icon {
    width: auto;
    height: 24px;
    padding-top: 3px;
}

#login-icon {
    width: auto;
    height: 24px;
    padding-top: 8px;
}

#logout-icon {
    width: auto;
    height: 32px;
    padding-top: 4px;
}

#cart-icon {
    width: auto;
    height: 30px;
    padding-top: 4px;
}

.search-results {
    position: absolute;
    top: 70px; /* Adjust based on your navbar height */
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
    padding-bottom: 20px;
    background-color: white;
    box-shadow: 0 2px 5px rgba(0,0,0,0.15);
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    z-index: 99; /* Below the navbar */
    max-height: 400px;
    overflow-y: scroll;
    border-top: 1px solid #ddd;
}

.search-results-item {
    width: 75%;
    max-height: 50px;
    display: flex;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 10px;
    border-bottom: 1px solid #ddd;
    cursor: pointer;
}

.search-results-item:hover {
    background-color: #f5f5f5;
}

.search-results-item h3 {
    margin: 0;
    font-size: 18px;
    color: #333;
    flex: 1;
    margin-right: 10px;
}

.search-results-item p {
    margin: 5px 0 0;
    font-size: 14px;
    color: #666;
    flex: 1;
    margin-right: 10px;
}
.search-results-item img{
    display: flex;
    max-width: 50px;
}